@mixin font($fontFamily, $src:'../fonts/', $ext:'.ttf'){
    font-family: $fontFamily;
    src:url($src + $fontFamily + $ext);
   }
  
  @mixin Bg-gradient($firstColor:$jg-Gold, $secondColor:$jg-Green ){
     background: $secondColor;
     background: -webkit-linear-gradient(transparentize($firstColor, .3), $secondColor);
     background: linear-gradient(transparentize($firstColor, .4) 20% , $secondColor 60%);
  }
  
  @mixin Btn-gradient($firstColor:transparentize($jg-Green, .3), $secondColor:$jg-Green){
     background: $secondColor;
     background: -webkit-linear-gradient(90deg, $firstColor 60%, $secondColor);
     background: linear-gradient(90deg, $firstColor 20%, $secondColor);
  }
  
  @mixin border($color: red){
     border: 1px solid $color;
  }
  
  @mixin position($top: 0em, $right: 0em, $bottom: 0em, $left: 0em){
     top: $top;
     left: $left;
     bottom: $bottom;
     right: $right;    
    
  }
  
  @mixin break($args...){
      @if length($args) == 1 {
        @media (min-width: nth($args, 1)){
            @content; 
          }	
      }
  
      @else{
      @media (min-width: nth($args, 1))
            and (max-width: nth($args, 2)){
            @content; 
          }	
      }
      
  }
  
  @mixin placeholder($color:#333, $position:'relative', $fontSize:15px){
          ::-webkit-input-placeholder{
          color: transparentize($color, .7);
          position:$position; 
          font-size:$fontSize; 
      }
      :moz-placeholder{
          color: transparentize($color, .7);
          position:$position; 
          font-size:$fontSize;
      }
      :ms-input-placeholder{
          color: transparentize($color, .7);
          position:$position; 
          font-size:$fontSize;
      }
  }
  
  @mixin clearfix{
      &:before,
      &:after{
          content: '';
          display: table;
      }
      &:after{ clear: both;}
  }

  @mixin spaced{
    width: 86%;
		position: relative;
		left: 7%;
		right: 7%;
		z-index: 120;
    
    
    @include break(0, $lg){
			width: 96%;
      left: 2%;
      right: 2%;
    }
  }
  
  @mixin card($depth: 1, $bg: #fff) {
    background: $bg;
    border-radius: 3px;    
      @if $depth < 1 {
      box-shadow: none;
    } @else if $depth > 5 {
      @warn "Invalid $depth `#{$depth}` for mixin `card`.";
    } @else {
      box-shadow: bottom-shadow($depth), top-shadow($depth);  
    }
  }
  
  @function top-shadow($depth) {
    $primary-offset: nth(1.5 3 10 14 19, $depth) * 1px;
    $blur: nth(1.5 3 10 14 19, $depth) * 4px;
    $color: rgba(#999, nth(.12 .16 .19 .25 .30, $depth));
  
    @return 0 $primary-offset $blur $color;
  }
  
  
  @function bottom-shadow($depth) {
    $primary-offset: nth(1.5 3 6 10 15, $depth) * 1px;
    $blur: nth(1 3 3 5 6, $depth) * 4px;
    $color: rgba(black, nth(.24 .23 .23 .22 .22, $depth));
  
    @return 0 $primary-offset $blur $color;
  }
  
  @mixin jg-grid($qty, $margin){
      width: ((100% - (($qty - 1) * $margin))/$qty);
      &:nth-child(n){
          margin-bottom: $margin;
          margin-right: $margin;
      }
      &:nth-child(#{$qty}n){
          margin-bottom: 0;
          margin-right: 0;
      }
  }
  
  @function calculateRem($size) {
    $remSize: $size / 16px;
    @return #{$remSize}rem ;
  }

  @mixin fontSize($size) {
    font-size: $size; //Fallback in px
    font-size: calculateRem($size);
    line-height: $size;
    line-height: calculateRem($size) + .01rem;
  }


  @mixin jgHover(){
    cursor: pointer;
		border: 4px solid transparent;
		transition: all .4s ease-in;
               
    &:hover { border-color: $jgGold; }
  }

  @mixin noDisplay(){
    &:before, &:after{
      display: none;
   }
  }


  @mixin fluid-type($properties, $min-vw, $max-vw, $min-value, $max-value) {
    & {
      @each $property in $properties {
        #{$property}: $min-value;
      }
  
      @media screen and (min-width: $min-vw) {
        @each $property in $properties {
          #{$property}: calc(#{$min-value} + #{strip-unit($max-value - $min-value)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
        }
      }
  
      @media screen and (min-width: $max-vw) {
        @each $property in $properties {
          #{$property}: $max-value;
        }
      }
    }
  }
  
  @function strip-unit($value) {
    @return $value / ($value * 0 + 1);
  }