/*------------------
  HEADER
-------------------*/

.kabod-header{
  padding-top: 0.6em;
  padding-bottom: 0.7em;
  position: relative;
  z-index: 10000;

  background-color: #fff;
  .gd-block{
    display: grid;
    grid-template-columns: 16% 8%;
    grid-column: 70%;
    
    @include break($md){
      grid-template-columns: 12% 50%;
      grid-column-gap: 35%;

    }
  }
}

.input-toggler{ display: none;}
.menu-toggler{
  position: absolute; 
  right: 7%; 
  top: 0em; 
  width: 80px; 
  height: 40px; 
  z-index: 3000; 
  display: flex; 
  justify-content: center; 
  align-items: center; 
  flex-direction: column; 
  cursor: pointer;
  
}

.kabod-header__hamburger{
  position: absolute;
  right: 4%;
  display: none;  

  @include break(0, $md){
    display: block;
  }

  & h5{
    background: $secondaryColor;
    color: #fff;
    padding: 0.4em 1.2em;
    border-radius: 25px;
  }
}

.menu-toggler-line{
  width: 60%; 
  height: 3px; 
  background: $secondaryColor !important; 
  margin: 0 0 10px 0px; 
  position: relative; 
  transition: all .40s ease-out;
}

.input-toggler:checked ~ .menu-toggler 
.menu-toggler-line {
  top: 8px; 
  translateY: 100px;  
  transform: rotate(45deg);
}

.input-toggler:checked ~ .menu-toggler 
.menu-toggler-line:nth-child(2) {
  display: none;
}

.input-toggler:checked ~ .menu-toggler 
.menu-toggler-line:nth-child(3) {
  top: -5px; 
  translateY: 10; 
  transform: rotate(135deg);
}

.kabod-nav{
  @include break(0, $md){
    position: fixed;
    width: 45%;
    height: 100%;
    top: 0;
    left: 0;
    @include card(3);
    background-color: $secondaryColor;
    transition: all .40s ease-out;
    z-index: 10000;
    opacity: 0.2;
    transform: translateX(-100%);
    
    }

    &.reveal{
      transform: translateX(0%);
      opacity: 0.98;
    }
}

.kabod-nav__list{
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;

  @include break(0, $md){
    margin-top: 80px;
    display: block;
    height: auto;
  }

  & .kabod-nav__item{
    margin: 0 1.2em;
    @include break(0, $md){
        margin-bottom: 2.5em;
    } 
    
    & a{
      font-size: $h5;

      &:hover{
        color: $secondaryColor;
      }

      @include break($md){
        &:after {
            content: '';
            display: block;
            height: 3px;
            width: 0;
            background: transparent;
            transition: width .5s ease, background-color .5s ease; /* .5 seonds for changes to the width and background-color */
            -webkit-transition: width .5s ease, background-color .5s ease; /* Chrome and Safari */
            -moz-transition: width .5s ease, background-color .5s ease; /* FireFox */
        }
        /* Change the width and background on hover, aka sliding out */
        &:hover:after {
            width: 100%;
            background: $secondaryColor;
        }
    }

      @include break(0, $md){
          padding: 15px 30px;
          display: block;
          font-size: $h3;
          color: #fff;
          
          &:hover{
            background: $primaryColor;
          }
      }    
    }
     
  }
}