.kabod-top,
.kabod-s-top{
    width: 100%;
    height: 90vh;
    position: relative;

    @include break(0, $sm){
        height: 50vh;
    }
}

.kabod-top__container{
    width: 100%;
    height: 100%;

    & .kabod-top__slides{
        width: 100%;
        height: 100%;
        background-size: cover;
        
        display: flex;
        align-items: center;
    }

    & .kabod-top__slidesblock{
        display: flex;
        flex-direction: column;
        position: relative;
        top: 7.5em;
        padding-left: 1.3em;

        @include break($md){
            width: 45%;
        }

        & h1, & p{
            color: #fff;
        }

        & h1{
            text-transform: uppercase;
        }

        & p{
            margin-top: 1.1em;
        }
    }
}

.kabod-top__slides1{
    background-image: url("../../img/slide-1.jpg");
}

.kabod-top__slides2{
    background-image: url("../../img/slide-2.jpg");
}

.kabod-top__slides3{
    background-image: url("../../img/slide-3.jpg");
}

.kabod-slidecontrols{
    position: absolute;
    top: 0;
    width: 10%;
    height: 100%;
    background: transparent;
    color: #fff;
    font-size: $h1;
    z-index: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.kabod-slidecontrols__prev{
  left: -3%;  
}

.kabod-slidecontrols__next{
    right: 4%;  

    @include break(0, $sm){
        right: -2%;
    }
}

.kabod-slidepager{
    position: absolute;
    bottom: 2.5em;
    z-index: 600;
    left: 40%;
    display: flex;

    & p{
        border: 2px solid #fff;
        border-radius: 100%;
        color: #fff;
        width: 20px;
        height: 20px;
        padding: 1px 3px 1px 4px;
        margin-right: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .cycle-pager-active{
        background-color: $secondaryColor;
        border-color: $secondaryColor;
    }


}

.kabod-block{
    width: 100%;
}    

.kabod-serviceshome{
    display: grid;
    grid-template-columns: 50% 50%;

    @include break(0, $md){
        grid-template-columns: 100%;
        grid-column-gap: 0;  
    }
}

.kabod-serviceshome__means{
    padding-top: 3.5em;

    & .gd-block{
        
    }

    & .kabod-serviceshome__list{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
    }

    & .kabod-serviceshome__item{
        display: grid;
        width: 80%;
        grid-template-columns: 25% 30%;
        grid-column-gap: 5%;
        margin-bottom: 2.5em;
        justify-content: center;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        padding-bottom: 1.6em;

        
    } 

    & .kabod-serviceshome__image{
        & img{
            width: 100%;
            border: 3px solid #000;
            border-radius: 100%;
            padding: 1.5em;
        }
    }

    & h2{
        display: flex;
        align-items: center;
        text-transform: uppercase;
    }
}

.kabod-serviceshome__desc{
    background: #000;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include break(0, $md){
        padding-top: 3.5em;
        padding-bottom: 3.5em;
    }

    & p{
        color: #fff;
        margin-bottom: 1.5em;
        font-size: $h4;
    }
}

.kabod-form{
    background-image: url("../../img/form-bg.jpg");
    background-size: cover;
    padding-top: 3.5em;
    padding-bottom: 3.5em;

    & .gd-block{
        display: grid;
        grid-template-columns: 30% 60%;
        grid-column-gap: 10%;

        @include break(0, $md){
            grid-template-columns: 100%;
            grid-column-gap: 0%;
        }
    }

    & .kabod-form__bg{
        display: flex;
        align-items: flex-start;
        
        @include break(0, $md){
            display: none;
        }
        
        & img{
            width: 100%;
        }
    }
}

.kabod-form__block{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-bottom: 1.5em;

    & label{
        color: #fff;
        font-family: $TitleFont;
    }

    & input{
        border: 1px solid #000;
        outline: none;
        border-radius: 20px;
        padding-left: 2.5em;
    }
}

.kabod-form__application{
    & button{
        width: 40%;
        font-size: $h5;
    }

    & h3{
        color: #fff;
        margin-bottom: 1.5em;
    }
}


.kabod-location{
    width: 100%;
    height: 100vh;
    position: relative;

    & .gd-block{
        position: absolute;
        top: 6.5em;
        width: 35%;
        border: 1px solid #000;
        background: #fff;
        padding: 0.4em 0.6em;

        @include break(0, $md){
            width: 80%;
        }
    }
}

.kabod-a-top{
    background-image: url('../../img/kabod-top.jpg');
    background-size: cover;
}

.kabod-a-top__block{
    display: grid;
    grid-template-columns: 40% 30%;
    grid-column-gap: 5%;
    padding-top: 2.5em;

    @include break(0, $md){
        grid-template-columns: 100%;
        grid-column-gap: 0;
    }
}

.kabod-a-top__figure{

    @include break(0, $md){
        display: none;
    }

    & img{
        width: 100%;
    }
}

.kabod-a-top__desc{
    display: flex;
    justify-content: center;
    flex-direction: column;

    @include break(0, $md){
        padding-bottom: 2.5em;
    }
    
    & h3,
    & p{
        color: #fff;
    }

    & p{
        margin-top: 1.4em;
    }
}

.kabod-s-top{
    background-image: url('../../img/kabod-s-top.jpg');
    background-size: cover;

    & .gd-block{
        height: 100%;
    }
}

.kabod-c-top{
    background-image: url('../../img/kabod-c-top.jpg');
    background-size: cover;

    & .gd-block{
        height: 100%;
    }
}



.kabod-s-top__block{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;

    & h1{
        color: #fff;
        text-align: center;
    }

    & .kabod-s-top__list{
        display: flex;
        margin-top: 3.5em;
        
        @include break(0, $md){
            flex-wrap: wrap;
            justify-content: center;
        }

        & .kabod-s-top__item{
            background: #fff;
            border-radius: 30px;
            padding: 0.3em 1.3em;
            margin: 0 0.4em;
            text-transform: uppercase;

            @include break(0, $md){
                margin: 0.8em 0.4em;
            }
        }
    }
}

.kabod-services{
    display: flex;
    justify-content: center;
    padding-top: 3.5em;
    padding-bottom: 3.5em;

    & .gd-block{
        display: grid;
        grid-template-columns: 35% 35%;
        grid-column-gap: 5%;

        @include break(0, $md){
            padding-right: 3.5em;
            grid-template-columns: 100%;
            grid-column-gap: 0%;
            grid-row-gap: 2.5em; 
        }
    }

    & .kabod-services__block{
        @include card(3);
        background-color: #eee;
    }

    & .kabod-services__desc{
        padding: 0.6em 0.7em;

        & p{
            margin-top: 1em;
        }
    }
}

.kabod-contact{
    padding-bottom: 3.5em;
    padding-top: 3.5em;

    & .gd-block{
        display: grid;
        grid-template-columns: 60% 30%;
        grid-column-gap: 5%;

        @include break(0, $md){
            grid-template-columns: 100%;
            grid-column-gap: 0%;
        }
    }

    & .kabod-form__application{
        & label,
        & h3{
            color: #000;
        }
    }

    & .kabod-contact__figure{
        display: flex;
        align-items: center;

        @include break(0, $md){
            display: none;
        }
    }
}