/* CSS Document */
html{
	text-rendering: optimizeLegibility;
	@include fluid-type(font-size, $xs,
	$lg, $minFont, $maxFont);
}

:root {
    counter-reset: masonry;
  }

body{
	// background-color: #fcfcfc;
