* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
	}
.gray{ background:#eee; }

/*------------------------
 Typography
-----------------*/

h1,
h2,
h3,
h4,
h5,
h6,
label{
	font-family: $TitleFont;
	letter-spacing: 0.12rem;
}

label{
	font-size: $h5;
	margin-bottom: 1em;
	font-weight: 700;
}

// Typography Scales
// https://www.modulgdcale.com/?16&px&1.333

h1{
	font-size: $h1;
	line-height: calc(#{$h1} + 4.5px);
	font-weight: 700;
}
h2{
	font-size: $h2;
	line-height: calc(#{$h2} + 4.5px);
	font-weight: 700;
}

h3{
	font-size: $h3;
	line-height: calc(#{$h2} + 4.5px);
	font-weight: 600;
}
h4{
	font-size: $h4;
	line-height: calc(#{$h3} + 4.5px);
	font-weight: 600;
}
h5{
	font-size: $h5;
	line-height: calc(#{$h4} + 4.5px);
	font-weight: 400;
}
h6{
	font-size: $h6;
	line-height: calc(#{$h6} + 4.5px);
	font-weight: 400;
}

p, li, input, select, textarea{
	font-family: $PrimaryFont;
	font-size: $textFontSize;
	line-height: calc(#{$h3});
	letter-spacing: 0.07rem;
	color: rgba(0, 0, 0, .8);
	font-weight: 400;

	@include break(0px, $md){
		font-size: 1.2rem;
	}
}

hr {
	outline:none;
	border:none;
	}

a{
	text-decoration: none;
	color: inherit;
	font-family: $PrimaryFont;
	font-size: $linkFontSize;
	font-weight: 300;
	transition: all .4s ease-in;
	letter-spacing: 0.12rem;

		@include break(0, $md){
			font-size: 1rem;
		}
	}

a:hover{
	text-decoration:none;
	color:inherit;
}

/*--------------------
COLORS
---------------------*/
.primary-color{
	color: $primaryColor;
}

.secondary-color{
	color: $secondaryColor;
}

.tertiary-color{
	color: $tertiaryColor;
}

/*--------------------
GLOBAL BLOCKS
---------------------*/
.gd-block{
	@include spaced();
}


%cta{
	background: $secondaryColor;
	padding: 1.2em 1.7em;
	border-radius: 30px;
	outline: none;
	border: none;
	font-family: $PrimaryFont;
	font-weight: 400;
	font-size: $linkFontSize;
	text-align: left;
	color: #fff;
	z-index: 350;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
	transition: all .4s ease-in;
	position: relative;

	@include break(0, $md){
		font-size: 0.7rem !important;
	}

	&:hover{
		background: $secondaryColor;
		color: #fff;
		text-decoration: none;
	  }
  }


.kabod-cta{
	@extend %cta;

	& .fas{
		font-size: 1.7rem;
		position: absolute;
		right: 5px;
		bottom: 11px;

		@include break(0, $lg){
			bottom: 5px;
		}
	}
}

%cta-small{
	border: 2px solid $secondaryColor;
	padding: 0.6em 1.6em .6em 1em !important;
	color: $secondaryColor;
	border-radius: 30px;

	&:hover{
		background: $secondaryColor;
		color: #fff;
		border-color: $secondaryColor;
	}
}

.jg-cta-small{
	@extend %cta-small; 
}

.jg-cta-white{
	@include card(1);
	color: $primaryColor;
	padding: 0.6em 1.6em .6em 1em;
	border-radius: 40px;
	border: none;
	transition: all .4s ease-in;
	font-size: $linkFontSize;
	cursor: pointer;

	@include break(0, $md){
		font-size: 1.1rem;
	}

	&:hover{
		background:  $primaryColor;
		color: #fff;
	}

}

.gd-topsection{
	background: #eee;

	& .gd-global-block{
		padding-top: 3.5em;
	}
}

.table{
	font-family: $PrimaryFont;
	font-size: $h4;

	& thead,
	& .thead{
		font-family: $TitleFont;
		font-weight: 700;
		background: $primaryColor;
		color: #fff !important;
	}

	& .thead{
		background:#eee;
	}

	& tr{
		& td{
			font-family: $PrimaryFont;
		}
	}
}

// ACCORDION


.gd-accordion{
	margin-top: 1.5em;
}

.gd-accordion-item {
  border-bottom: 1px solid #e5e5e5;
	padding: 1.5em 1em 1.1em 1em;
	@include card(1);
}
.accordion .accordion-item button[aria-expanded='true'] {
  border-bottom: 1px solid #03b5d2;
}
.su-spoiler-title {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 1em 0;
  color: $primaryColor;
	font-family: $PrimaryFont;
  font-size: 1rem !important;
  font-weight: 400;

  background: none;
  outline: none;
}

.su-spoiler-closed .su-spoiler-title{

}

.gd-accordion-content p {
  font-size: 0.8rem;
}

.gd-accordion-content a{
	color: $secondaryColor;
	font-size: inherit;
}

.gd-accordion-content span {
  font-weight: bold;
}
.gd-accordion-content li{
	margin-bottom: 1em;
}

.gd-packages-table{
	& tbody{
		& tr{
			& td{
				color: $primaryColor;
				font-weight: 700;
			}
		}
	}
}

.gd-i-block{
    @include spaced();
    padding-top: 2em;
    padding-bottom: 2em;
}

