@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Noto+Sans:wght@400;700&display=swap');

/*------------------------
 Color Palette
-----------------*/
$primaryColor: #000;
$secondaryColor: #EE6767;
$tertiaryColor: #f2f0e6;
$accentColor: #600001;

$border: 1px solid red;
$transition: all .4s ease-in;

/*----------------------
  Typography
  --------------------*/
  $PrimaryFont: 'Didact Gothic', sans-serif;
  $TitleFont: 'Noto Sans', sans-serif;
  
  //  Break-points
  
  $xs: 320px;
  $sm: 576px;
  $md: 992px;
  $lg: 1279px;
  $xl: 1535px;


  $baseline: 1.5rem;
  $minFont: 10px;
  $maxFont: 20px;

  $h1: 3.052rem;
  $h2: 2.441rem;
  $h3: 1.953rem;
  $h4: 1.563rem;
  $h5: 0.8rem;
  $h6: 0.6rem;
  $textFontSize: 1rem;
  $linkFontSize: 0.7rem;